<template>
  <div id="Index">
    <el-container>
      <div :class="collapsed?'menu menu-collapsed':'menu menu-expanded'">
        <el-row class="tac">
          <el-col :span="24">
            <h5 class="top"></h5>
            <el-menu
              default-active="2"
              class="el-menu-vertical-demo"
              :collapse="collapsed"
              @open="handleOpen"
              @close="handleClose">
              <el-submenu index="1">
                <template slot="title">
                  <i class="el-icon-location"></i>
                  <span></span>
                </template>
                <el-menu-item-group>
                  <template slot="title"></template>
                  <el-menu-item index="1-1">1</el-menu-item>
                  <el-menu-item index="1-2">2</el-menu-item>
                </el-menu-item-group>
                <el-menu-item-group title="2">
                  <el-menu-item index="1-3">3</el-menu-item>
                </el-menu-item-group>
                <el-submenu index="1-4">
                  <template slot="title">4</template>
                  <el-menu-item index="1-4-1">1</el-menu-item>
                </el-submenu>
              </el-submenu>
              <el-menu-item index="2">
                <i class="el-icon-menu"></i>
                <span slot="title"></span>
              </el-menu-item>
              <el-menu-item index="3">
                <i class="el-icon-setting"></i>
                <span slot="title"></span>
              </el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
      </div>
      <el-container>
        <el-header>
          <div class="tools" v-on:click="collapsed = !collapsed">
            <el-button type="primary" icon="fa fa-align-justify"></el-button>
          </div>
        </el-header>
        <el-main>Main</el-main>
        <el-footer height="30px">
          <div class="pull-inherit">
            <div class="ng-binding">
                <strong>Copyright</strong> Shanghai&nbsp;MTC&nbsp;Information Technology Co.,Ltd  2017
            </div>
          </div>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  data(){
    return {
      collapsed : false
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  }
  }
</script>

<style>
  body{
    margin: 1px;
    font-family: "open sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
    color: #676a6c;
    overflow-x: hidden;
  }

  .pull-inherit{
    float: inherit!important;
  }

  .top{
    background-color: #409EFF;
    height:60px;
    border-right-style:solid;
    border-right-width: 1px;
    margin: 0;
    color:#FFF;
    line-height: 60px;
  }

  .el-header{
    background-color: #409EFF;
    color: #fff;
    text-align: center;
    line-height: 60px;
    padding: 0;
  }
  .el-footer {
    background-color: #FFF;
    color: #676a6c;
    text-align: center;
    padding: 0;
    font-size: 13px;
  }
  .el-main {
    background-color: #FFF;
    color: #333;
    text-align: center;
  }
  .el-container {
    min-height: 99vh;
  }

  .menu-expanded{
    flex:0 0 200px;
    width: 200px;
  }
  .menu-collapsed{
    flex:0 0 65px;
    width: 64px;
  }
  .el-container .tools{
      width:14px;
      height: 60px;
      line-height: 60px;
      cursor: pointer;
    }
</style>



