<template>
  <div id="MarkDownEditor">
    <mavon-editor :ishljs="true" @change="change"/>
    ------------
    <mavon-editor default_open="preview" :toolbarsFlag="false" :subfield="false" v-model="msg">
    </mavon-editor>
  </div>
</template>

<script>
export default {
  data(){
    return {
      msg:""
    }
  },
  methods: {
    change:function(value,render){
      console.log(value);
      this.msg = value;
    }
  }
}
</script>

<style>
</style>



